  background-color: #fef6e8;
  font-weight: 500;
}

.accordion-button:not(.collapsed) {
  color: #fff;
  font-weight: 500;
  /* background-color: #EAF8EF; */
  background-color: #44a694;
}

.accordion-button-belanja:not(.collapsed) {
  color: #fff;
  font-weight: 500;
  /* background-color: #EAF8EF; */
  background-color: #e58b20 !important;
}

.progress {
  background-color: #fff;
}

.card-demografi-penduduk {
  border: none;
  background-color: #eaf7ee;
  border-radius: 10px;
}

.card-demografi-penduduk__info,
.card-demografi-penduduk__jumlah {
  font-size: 1.3rem;
  font-weight: 500;
}

#chartPendudukDusun {
  width: 100%;
  height: 500px;
}

.penduduk-card {
  border-radius: 10px;
  border: none;
  background-color: #eaf7ee;
  height: 200px;
}

.penduduk-card__jumlah {
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2.5rem;
  margin-top: 2rem;
}

.penduduk-card__info {
  font-size: 1.25rem;
  font-weight: 500;
}

.tg-wrap table {
  display: block;
  height: 425px;
  overflow-y: scroll;
}

.tg-wrap th {
  background-color: #44a694;
  padding: 15px;
  color: #fff;
  position: sticky;
  top: 0px;
}

.tg-wrap th:first-child {
  border-radius: 10px 0px 0px 0px;
}

.tg-wrap th:last-child {
  border-radius: 0px 10px 0px 0px;
}

.tg-wrap .th-1 {
  width: 90%;
}

.tg-wrap .th-2 {
  width: 10%;
}

.tg-wrap tr {
  border-bottom: 1px solid #c4c4c4;
}

.tg-wrap td {
  padding: 15px;
}

.tg-wrap .td-center {
  text-align: center;
}

.input-group:not(.has-validation) > .dropdown-toggle:nth-last-child(n + 3),
.input-group:not(.has-validation)
  > :not(:last-child):not(.dropdown-toggle):not(.dropdown-menu) {
  border-radius: 20px 0px 0px 20px;
  background-color: #fff;
}

.input-group-lg > .btn,
.input-group-lg > .form-control,
.input-group-lg > .form-select,
.input-group-lg > .input-group-text {
  font-size: 0.95rem;
  border-radius: 0px 20px 20px 0px;
}

.jumbo-kontenBelanja {
  font-size: 2.5rem;
  font-weight: 500;
  line-height: 1.2;
}

.img-kontenBelanja {
  z-index: 2;
}

.belanja-dots {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 0;
}

.page-link {
  border: none;
  color: #202020;
}

.page-item.active .page-link {
  background-color: #e58b20;
  border-color: #e58b20;
  border-radius: 3px;
}

.wrap-jumlah-accordion {
  padding-right: 0px !important;
}

.navbar-nav {
  align-items: center;
}

