@media only screen and (max-width: 1225px) {
  .nama-desa-lite {
    font-size: 12px;
  }
  .info-nav {
    font-size: 11px !important;
  }
  .nav-link {
    font-size: 12px;
    padding-right: 15px !important;
    padding-left: 15px !important;
  }
}

/* Responsive Tablet */
@media only screen and (max-width: 821px) {
  .navbar-nav {
    padding-top: 1.5rem;
  }

  .vertical-center {
    width: 70%;
  }

  #pengumuman,
  #wisataDesa,
  #potensiDesa,
  #petaDesa,
  #profilPemerintahan {
    min-height: 60vh;
  }

  #administrasiUmum,
  #administrasiKeuangan,
  #administrasiPenduduk,
  #jumlahSurat,
  #fokusPengembanganDesa,
  #youtubeSection,
  #instagramSection,
  #twitterSection,
  #petaLokasiDesa {
    min-height: 70vh;
  }

  .jelajahi-desa-card:hover {
    transform: scale(1);
  }

  .pengembangan-judul {
    height: 75px;
  }

  .input-group-lg > .btn,
  .input-group-lg > .form-control,
  .input-group-lg > .form-select,
  .input-group-lg > .input-group-text {
    font-size: 1.1rem !important;
  }

  .img-kontenBelanja {
    display: none;
  }

  .jumbo-kontenBelanja {
    padding: 15px;
    font-size: 1.75rem;
  }

  .info-accordion {
    font-weight: bold;
  }

  .card-demografi-penduduk img {
    height: 85px !important;
  }

  .penduduk-card__info {
    font-size: 1rem;
  }
}

/* Responsive Mobile */
@media only screen and (max-width: 600px) {
  #jumbotron {
    margin-top: -51px;
  }

  #logoNavbar {
    height: 30px;
  }

  .nama-desa-lite {
    font-size: 0.8rem;
  }

  #visiMisi .center-v {
    margin-bottom: 2rem;
  }

  .accordion .col-md-6 {
    margin-bottom: 5px;
  }
}

